import { useEffect, useState } from "react";
import {
  BadgeDelta,
  Card,
  //DeltaType,
  Dropdown,
  DropdownItem,
  Datepicker,
  //Flex,
  MultiSelectBox,
  MultiSelectBoxItem,
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeaderCell,
  TableRow,
  TableFoot,
  Flex,
  TabList,
  Tab,
  Title,
  Metric,
  CategoryBar,
  Legend,
  Text,
  ColGrid,
  Col,
  ProgressBar,
  Subtitle,
  Divider,
  Block,
  Icon,
  Badge,
} from "@tremor/react";

import moment from "moment";
import axios from "axios";
import { isArray, isFinite, isObject } from "lodash";
import { CSVLink, CSVDownload } from "react-csv";
import ExpenseButtonActions from "../components/ExpenseButtonActions";
import NewExpenseModal from "../components/NewExpenseModal";

export default function TableView() {
  const columns = [
    { key: "journal_transaction_id", name: "Transaction No" },
    { key: "account_name", name: "Type" },
    { key: "sub_cat_name", name: "Category" },
    { key: "narrative", name: "Narrative" },
    { key: "transaction_date", name: "Transaction Date" },
    { key: "date_modified", name: "Date Created" },
    { key: "debit_amount", name: "Amount" },
  ];
  const [expenses, setExpenses] = useState([]);
  const [fiscalYear, setFiscalYear] = useState({});
  const [selectedNames, setSelectedNames] = useState([]);
  const [selectedAccountNames, setSelectedAccountNames] = useState([]);
  const [selectedCategory, setSelectedCategories] = useState([]);
  const [selectedTransactionDate, setSelectedTransactionDates] = useState([]);

  const [selectedColumns, setSelectedColumns] = useState([]);
  const [startDate, setStartDate] = useState("");
  const [endDate, setEndDate] = useState("");
  const [lastUpdatedAt, setLastUpdatedAt] = useState("");
  const [isLoading, setIsLoading] = useState("");
  const [error, setError] = useState("");
  const [filteredExpenses, setFilteredExpenses] = useState([]);
  const [sums, setSums] = useState({});
  const [activeTab, setActiveTab] = useState("");
  const [summaryType, setSummaryType] = useState("full_org");
  const [summaryData, setSummaryData] = useState({});
  const [showNewExpenseModal, setShowNewExpenseModal] = useState(false);

  const isAccountNameSelected = (account_name) =>
    selectedAccountNames.includes(account_name) ||
    selectedAccountNames.length === 0;

  const isTransactionDateSelected = (date) =>
    selectedTransactionDate.includes(date) ||
    selectedTransactionDate?.length === 0;

  const isCategorySelected = (category) =>
    selectedCategory.includes(category) || selectedCategory?.length === 0;

  const isColumnSelected = (column) =>
    selectedColumns.includes(column) || selectedColumns?.length === 0;

  const fetchExpensesData = () => {
    setIsLoading("Fetching Expenses...");
    let formData = new FormData();
    if (startDate) {
      formData.append("start_date", startDate);
    }
    if (endDate) {
      formData.append("end_date", endDate);
    }

    axios
      .post("/Expenses/transactions", formData)
      .then((response) => {
        if (response.data) {
          let responseData = response.data;
          console.log(responseData);

          setExpenses(responseData.data.expenses);
          setFiscalYear(responseData.data.fiscal_year);
          // console.log(creditOfficerData)
          setIsLoading("");
        } else {
          throw new Error("Something Went wrong while fetching expenses");
        }
      })
      .catch((err) => {
        console.log(err);
        setError("Something Went wrong while fetching expenses");
        setIsLoading("");
      });
  };

  useEffect(() => {
    fetchExpensesData();
  }, [startDate, endDate]);

  useEffect(() => {
    let filteredData = expenses
      ?.filter((item) => isAccountNameSelected(item.account_name))
      ?.filter((item) => isCategorySelected(item.sub_cat_name))
      ?.filter((item) => isTransactionDateSelected(item.transaction_date));

    setFilteredExpenses(filteredData);
  }, [
    expenses,
    selectedColumns,
    selectedAccountNames,
    selectedCategory,
    selectedTransactionDate,
  ]);

  useEffect(() => {
    let columnSums = {};
    filteredExpenses.forEach((item) => {
      Object.keys(item).forEach((key) => {
        if (["debit_amount"].includes(key)) {
          if (!isNaN(item[key])) {
            columnSums[key] =
              parseFloat(columnSums[key] ? columnSums[key] : 0) +
              parseFloat(item[key] ? item[key] : 0);
            columnSums[item.sub_cat_name] =
              parseFloat(
                columnSums[item.sub_cat_name]
                  ? columnSums[item.sub_cat_name]
                  : 0
              ) + parseFloat(item[key] ? item[key] : 0);
          }
        }
      });
    });

    console.log("SUMS : ", columnSums);

    setSums(columnSums);
  }, [filteredExpenses]);

  return (
    <div className="w-full h-full mt-2">
      <Card>
        {error && (
          <div className="shadow-lg alert alert-error">
            <div>
              <svg
                xmlns="http://www.w3.org/2000/svg"
                className="flex-shrink-0 w-6 h-6 stroke-current"
                fill="none"
                viewBox="0 0 24 24"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth="2"
                  d="M10 14l2-2m0 0l2-2m-2 2l-2-2m2 2l2 2m7-2a9 9 0 11-18 0 9 9 0 0118 0z"
                />
              </svg>
              <span>Error! {error}</span>
            </div>
          </div>
        )}
        {isLoading && (
          <div className="bg-black opacity-60 w-full h-full flex flex-col justify-center items-center absolute top-0 bottom-0 left-0 right-0 z-[1000]">
            <div className="flex justify-center py-2 font-bold text-white">
              {isLoading}
            </div>
            <div>
              <progress className="w-56 progress"></progress>
            </div>
          </div>
        )}
        <div
          className={`flex items-center py-4 px-2 justify-between  sticky z-10 w-full right-4 left-4 top-0 bg-white shadow-sm`}
        >
          <span className="">Expense Transactions</span>
          {/* <CSVLink
            className="relative inline-flex items-center px-8 py-3 overflow-hidden text-blue-600 border border-current rounded min-w-fit group focus:outline-none focus:ring active:text-blue-500"
            target="_blank"
            data={filteredExpenses}
            filename={"Expenses-Report.csv"}
          >
            <span className="absolute left-0 transition-transform -translate-x-full group-hover:translate-x-4">
              <svg
                className="w-5 h-5"
                xmlns="http://www.w3.org/2000/svg"
                fill="none"
                viewBox="0 0 24 24"
                stroke="currentColor"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth="2"
                  d="M17 8l4 4m0 0l-4 4m4-4H3"
                />
              </svg>
            </span>

            <span className="text-sm font-medium transition-all group-hover:ml-4">
              Export to CSV
            </span>
          </CSVLink> */}
          <ExpenseButtonActions
            csvData={filteredExpenses}
            newExpenseHandler={() => {
                // setShowNewExpenseModal(true)
            }}
          />
        </div>
        <div className="flex items-end justify-between w-full py-1"></div>

        <div className="w-full max-h-full">
          <div className="hidden sm:mt-6 sm:flex sm:justify-start sm:space-x-2">
            <Datepicker
              placeholder="Select Date range"
              enableRelativeDates={true}
              handleSelect={(startDate, endDate) => {
                setStartDate(moment(startDate).format("YYYY-MM-DD"));
                setEndDate(moment(endDate).format("YYYY-MM-DD"));
              }}
              defaultStartDate={null}
              defaultEndDate={null}
              minDate={null}
              maxDate={null}
              color="blue"
              maxWidth="max-w-none"
              marginTop="mt-0"
            />
            <MultiSelectBox
              handleSelect={(value) => setSelectedCategories(value)}
              placeholder="Category"
              maxWidth="max-w-full"
            >
              {[...new Set(expenses?.map((item) => item.sub_cat_name))]?.map(
                (item) => (
                  <MultiSelectBoxItem key={item} value={item} text={item} />
                )
              )}
            </MultiSelectBox>
            <MultiSelectBox
              handleSelect={(value) => setSelectedAccountNames(value)}
              placeholder="Type"
              maxWidth="max-w-xs"
            >
              {[...new Set(expenses?.map((item) => item.account_name))]?.map(
                (item) => (
                  <MultiSelectBoxItem key={item} value={item} text={item} />
                )
              )}
            </MultiSelectBox>

            <MultiSelectBox
              handleSelect={(value) => setSelectedTransactionDates(value)}
              placeholder="Transaction Date"
              maxWidth="max-w-full"
            >
              {[
                ...new Set(expenses?.map((item) => item.transaction_date)),
              ]?.map((item) => (
                <MultiSelectBoxItem key={item} value={item} text={item} />
              ))}
            </MultiSelectBox>

            <MultiSelectBox
              handleSelect={(value) => setSelectedColumns(value)}
              placeholder="Columns"
              maxWidth="max-w-xs"
            >
              {columns?.map((item) => (
                <MultiSelectBoxItem
                  key={item.key}
                  value={item.key}
                  text={item.name}
                />
              ))}
            </MultiSelectBox>
          </div>

          <div className="mt-6 space-y-2 sm:hidden sm:space-y-0">
            <Datepicker
              placeholder="Select Date range"
              enableRelativeDates={true}
              handleSelect={(startDate, endDate) => {
                setStartDate(moment(startDate).format("YYYY-MM-DD"));
                setEndDate(moment(endDate).format("YYYY-MM-DD"));
              }}
              defaultStartDate={null}
              defaultEndDate={null}
              minDate={null}
              maxDate={null}
              color="blue"
              maxWidth="max-w-none"
              marginTop="mt-0"
            />
            <MultiSelectBox
              handleSelect={(value) => setSelectedCategories(value)}
              placeholder="Category"
              maxWidth="max-w-full"
            >
              {[...new Set(expenses?.map((item) => item.sub_cat_name))]?.map(
                (item) => (
                  <MultiSelectBoxItem key={item} value={item} text={item} />
                )
              )}
            </MultiSelectBox>
            <MultiSelectBox
              handleSelect={(value) => selectedAccountNames(value)}
              placeholder="Type"
              maxWidth="max-w-xs"
            >
              {[...new Set(expenses?.map((item) => item.account_name))]?.map(
                (item) => (
                  <MultiSelectBoxItem key={item} value={item} text={item} />
                )
              )}
            </MultiSelectBox>

            <MultiSelectBox
              handleSelect={(value) => setSelectedTransactionDates(value)}
              placeholder="Transaction Date"
              maxWidth="max-w-full"
            >
              {[
                ...new Set(expenses?.map((item) => item.transaction_date)),
              ]?.map((item) => (
                <MultiSelectBoxItem key={item} value={item} text={item} />
              ))}
            </MultiSelectBox>

            <MultiSelectBox
              handleSelect={(value) => setSelectedColumns(value)}
              placeholder="Columns"
              maxWidth="max-w-xs"
            >
              {columns?.map((item) => (
                <MultiSelectBoxItem
                  key={item.key}
                  value={item.key}
                  text={item.name}
                />
              ))}
            </MultiSelectBox>
          </div>

          <Card marginTop="mt-2">
            <div className="max-h-[45vh] w-full max-w-full overflow-y-scroll overscroll-auto">
              <Flex
                marginTop="mt-2"
                justifyContent="justify-between"
                alignItems="items-center"
                spaceX="space-x-2"
              >
                <Badge
                  text={`Showing ${Intl.NumberFormat().format(
                    filteredExpenses.length
                  )} of ${Intl.NumberFormat().format(expenses.length)} rows`}
                  color="gray"
                />
              </Flex>
              <Table marginTop="mt-0">
                <TableHead>
                  <TableRow>
                    {isColumnSelected("journal_transaction_id") ? (
                      <TableHeaderCell>Transaction No.</TableHeaderCell>
                    ) : null}
                    {isColumnSelected("debit_amount") ? (
                      <TableHeaderCell>Amount</TableHeaderCell>
                    ) : null}
                    {isColumnSelected("transaction_date") ? (
                      <TableHeaderCell>Transaction Date</TableHeaderCell>
                    ) : null}

                    {isColumnSelected("narrative") ? (
                      <TableHeaderCell>Narrative</TableHeaderCell>
                    ) : null}
                    {isColumnSelected("account_name") ? (
                      <TableHeaderCell>Type</TableHeaderCell>
                    ) : null}
                    {isColumnSelected("sub_cat_name") ? (
                      <TableHeaderCell>Category</TableHeaderCell>
                    ) : null}
                  </TableRow>
                </TableHead>

                <TableBody>
                  {filteredExpenses?.map((item) => (
                    <TableRow key={item.id}>
                      {isColumnSelected("journal_transaction_id") ? (
                        <TableCell>#{item.journal_transaction_id}</TableCell>
                      ) : null}
                      {isColumnSelected("debit_amount") ? (
                        <TableCell>
                          {Intl.NumberFormat().format(item.debit_amount)}
                        </TableCell>
                      ) : null}
                      {isColumnSelected("transaction_date") ? (
                        <TableCell>{item.transaction_date}</TableCell>
                      ) : null}
                      {isColumnSelected("narrative") ? (
                        <TableCell>{item.narrative || "-"}</TableCell>
                      ) : null}
                      {isColumnSelected("account_name") ? (
                        <TableCell>{item.account_name}</TableCell>
                      ) : null}
                      {isColumnSelected("sub_cat_name") ? (
                        <TableCell>{item.sub_cat_name}</TableCell>
                      ) : null}
                    </TableRow>
                  ))}
                </TableBody>
              </Table>
            </div>
          </Card>
          <div className="w-full py-4">
            <Card>
              <Flex justifyContent="justify-start" spaceX="space-x-2">
                <Title>Totals</Title>
                <Badge
                  text={`${Intl.NumberFormat().format(
                    filteredExpenses.length
                  )} of ${Intl.NumberFormat().format(expenses.length)} rows`}
                  color="gray"
                />
              </Flex>

              <ColGrid
                marginTop="mt-2"
                numColsSm={1}
                numColsLg={1}
                gapX="gap-x-2"
                gapY="gap-y-2"
              >
                {Object.keys(sums).map((key) => {
                  if (key !== "debit_amount") return;
                  return (
                    <Card key={key}>
                      <Text>{"Total Expenses"}</Text>
                      <Metric>
                        {Intl.NumberFormat().format(
                          sums[key]?.toFixed(2) || "0"
                        )}
                      </Metric>
                    </Card>
                  );
                })}
              </ColGrid>
              <ColGrid
                marginTop="mt-2"
                numColsSm={3}
                numColsLg={5}
                gapX="gap-x-2"
                gapY="gap-y-2"
              >
                {Object.keys(sums).map((key) => {
                  if (key === "debit_amount") return;
                  return (
                    <Card key={key}>
                      <Text>{key}</Text>
                      <Metric>
                        {Intl.NumberFormat().format(
                          sums[key]?.toFixed(2) || "0"
                        )}
                      </Metric>
                    </Card>
                  );
                })}
              </ColGrid>
            </Card>
          </div>
        </div>
      </Card>

      <NewExpenseModal
        show={showNewExpenseModal}
        toggle={setShowNewExpenseModal}
      />
    </div>
  );
}
